*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

html {
  scroll-behavior: smooth; }

body {
  margin: 0;
  font-weight: normal;
  font-size: 14px;
  line-height: 1.5;
  font-family: "Roboto Condensed", "Helvetica", "Arial", sans-serif;
  color: #ffffff; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.gradient-bg {
  background: url("../img/bg.jpg") center no-repeat #511fc4e5;
  background-size: cover; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0); }

.container {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px; }

@media screen and (max-width: 1199px) {
  .container {
    max-width: 1199px;
    padding: 0 45px; } }

@media screen and (max-width: 767px) {
  .container {
    max-width: 767px;
    min-width: 320px;
    padding: 0 15px; } }

.button {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-weight: bold;
  font-size: 12px;
  line-height: 14px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff; }

.header {
  padding-top: 28px;
  font-family: "Bebas Neue", "Helvetica", "Arial", sans-serif;
